import { React, useRef, useState, useEffect } from "react";
import { FaChevronDown } from "react-icons/fa";
import Facets from "./Facets";

//open / close filter drawer
const CompletionStatusFacetGroups = ({ name }) => {
  const [showFacets, setShowFacets] = useState(false);
  const containerRef = useRef(null);
  const facetRef = useRef(null);

  // const openFilterDrawer = (e) => {
  useEffect(() => {
    const facetHeight = facetRef.current.getBoundingClientRect().height;
    if (showFacets) {
      containerRef.current.style.height = `${facetHeight}px`;
    } else {
      containerRef.current.style.height = "0px";
    }
  }, [showFacets]);

  return (
    //look at using {facetgroup name = region to return below....do same for completion status}
    <>
      <div className="filter-group" onClick={() => setShowFacets(!showFacets)}>
        <p> {name} </p>
        <FaChevronDown className="fa-chevron-down" />
      </div>
      <div className="filter-wrapper-complete" ref={containerRef}>
        <div className="filter-options-complete" ref={facetRef}>
          <Facets name="Complete" />
          <Facets name="Not Complete" />
        </div>
      </div>
    </>
  );
};

export default CompletionStatusFacetGroups;
