import React from "react";

const FinishTimeInput = ({ pdpMountain, setFinishTime, finishTime }) => {
  return (
    <div>
      <label className="pdp-label">Finish Time</label>
      <input
        className="input"
        type="time"
        defaultValue={
          pdpMountain.finishTime === "" ? finishTime : pdpMountain.finishTime
        }
        onBlur={(e) => setFinishTime(e.target.value)}
      />
    </div>
  );
};

export default FinishTimeInput;
