import { React } from "react";
import { FaChevronLeft, FaChevronRight } from "react-icons/fa";
import { useLocation } from "react-router-dom";

const ImageCarousel = ({
  mountain,
  index,
  handleLeftClick,
  handleRightClick,
  pdpMountain,
  selectedMountain,
}) => {
  const { pathname } = useLocation();
  const isListerPage = pathname === "/lister";

  return (
    <div className="image-container">
      <FaChevronLeft
        className="fas fa-angle-left"
        onClick={() =>
          isListerPage
            ? handleLeftClick(index, mountain.name)
            : handleLeftClick(selectedMountain, pdpMountain.name)
        }
      />
      <img
        src={
          isListerPage
            ? mountain.image[mountain.imageNumber]
            : pdpMountain.image[pdpMountain.imageNumber]
        }
        className="mountain-image"
        alt={isListerPage ? mountain.name : pdpMountain.name}
      />
      <FaChevronRight
        className="fas fa-angle-right"
        onClick={() =>
          isListerPage
            ? handleRightClick(index, mountain.name)
            : handleRightClick(selectedMountain, pdpMountain.name)
        }
      />
    </div>
  );
};

export default ImageCarousel;
