import { React, useEffect } from "react";

const TopInputFields = ({
  pdpMountain,
  value,
  summitTime,
  startTime,
  ascentDuration,
  setSummitTime,
}) => {
  console.log(pdpMountain);

  useEffect(() => {
    if (startTime === "" || ascentDuration === "") {
      return;
    }
    function convertNST(startTime) {
      const arr = startTime.split(":");
      const seconds = arr[0] * 3600 + arr[1] * 60;
      return seconds;
    }

    function convertAD(ascentDuration) {
      const arr = ascentDuration.split(":");
      const seconds = arr[0] * 3600 + arr[1] * 60;
      return seconds;
    }

    const summitTimeCalc = convertNST(startTime) + convertAD(ascentDuration);

    var measuredTime = new Date(null);
    measuredTime.setSeconds(summitTimeCalc); // specify value of SECONDS
    console.log(summitTimeCalc);
    var summitTimeString = measuredTime.toISOString().substr(11, 8);

    setSummitTime(summitTimeString);
    console.log(summitTimeString);
  }, [ascentDuration, startTime]);

  return (
    <div>
      <label className="pdp-label">{value.title}</label>
      <input
        className="input"
        type={value.title === "Date" ? "date" : "time"}
        onBlur={(e) => {
          console.log(value.state, pdpMountain);
          value.setState(e.target.value);
        }}
        defaultValue={
          pdpMountain.summitTime === "" ? summitTime : value.stateName
        }

        //defaultValue={value.stateName}

        //value.title === "Summit Time" ? summitTime : value.stateName

        //       pdpMountain.summitTime === ""
        //         ? summitTime
        //         : pdpMountain.summitTime
        //     }
      />
    </div>
  );
};

export default TopInputFields;
