import React from "react";

const DoneButton = ({
  mountains,
  setMountains,
  activeFilter,
  setActiveFilter,
}) => {
  const clickDone = () => {
    // filter mountains based on ticks
    const filter = document.querySelectorAll(".filter");
    const filterArray = [];

    filter.forEach((filter) => {
      const filterInnerText = filter.innerText;
      if (filter.firstElementChild.classList.contains("show-fa-check")) {
        filterArray.push(filterInnerText);
      } else {
        return;
      }
      console.log(filterArray);
    });

    const searchMountains = mountains.filter((mountain) => {
      if (
        filterArray.length === 0 ||
        (filterArray.includes("Complete") &&
          filterArray.includes("Not Complete"))
      ) {
        return mountains;
      } else if (
        filterArray.includes(mountain.region) &&
        filterArray.includes("Not Complete")
      ) {
        console.log(mountain);
        return mountain;
      } else if (filterArray.includes(mountain.region)) {
        return mountain.region;
      } else if (filterArray.includes("Complete")) {
        return mountain.complete === true;
      } else if (filterArray.includes("Not Complete")) {
        return mountain.complete === false;
      } //doesn't work

      //need to cater for region and complete, region and not complete
    });

    //need to cater for region and complete, region and not complete

    setMountains(searchMountains);
    setActiveFilter(!activeFilter);
    const section = document.querySelectorAll("section");
    section.forEach((section) => {
      section.classList.remove("opaque-background");
    });
  };

  return (
    <div className="btn-container" onClick={clickDone}>
      <button className="done-btn" alt="done button">
        Done
      </button>
    </div>
  );
};

export default DoneButton;
