import React, { useState } from "react";
import Title from "./Title";
import Menu from "../Menu/Menu";
import Search from "./Search";
import { FaBars } from "react-icons/fa";
import BackButton from "./BackButton";
import Sort from "./Sort";
import { useLocation } from "react-router-dom";

const Header = ({
  mountains,
  setMountains,
  activeFilter,
  setActiveFilter,
  searchString,
  setSearchString,
}) => {
  const { pathname } = useLocation();
  const isListerPage = pathname === "/lister";

  const handleScroll = () => {
    const navHeader = document.querySelector(".nav-header");
    const secondNav = document.querySelector(".second-nav");
    const nav = document.querySelector(".nav");
    const scrollDepth = window.scrollY;
    const secondNavHeight = secondNav.getBoundingClientRect().height;
    // console.log(scrollDepth);

    window.addEventListener("scroll", handleScroll);

    //add sticky second nav when user scrolls up
    if (this.oldScroll > this.scrollY && scrollDepth > 30) {
      nav.classList.add("fixed-nav");
    } else {
      nav.classList.remove("fixed-nav");
    }
    this.oldScroll = this.scrollY;

    //   sticky top header when user scrolls down
    if (!nav.classList.contains("fixed-nav") && scrollDepth > secondNavHeight) {
      navHeader.classList.add("fixed-nav");
    } else {
      navHeader.classList.remove("fixed-nav");
    }
  };

  //Remove background and filter when the filters are open and the user scrolls / clicks on the product card
  const section = document.querySelectorAll("section");
  const closeFilter = () => {
    if (pathname === "/lister") {
      setActiveFilter(false);
    }
  };

  window.addEventListener("scroll", closeFilter);
  section.forEach((section) => {
    section.addEventListener("click", closeFilter);
  });

  //adding sort by functionality
  const [sort, setSort] = useState([
    "A-Z",
    "Z-A",
    "Height: High - Low",
    "Height: Low - High",
    "Difficulty: High - Low",
    "Difficulty: Low - High",
  ]);

  const handleSort = (sortOrder) => {
    //taking in parameter of e.target.value
    let result = [];
    switch (sortOrder) {
      case "A-Z":
        result = mountains.sort((a, b) => {
          if (a.name > b.name) {
            console.log(a.name, b.name);
            return 1;
          } else {
            return -1;
          }
        });
        break;
      // result = mountains.sort(); //shorthand for A-Z
      case "Z-A":
        result = mountains.sort((a, b) => {
          if (a.name > b.name) {
            console.log(a.name, b.name);
            return -1;
          } else {
            return 1;
          }
        });
        break;
      // result = mountains.sort().reverse(); //shorthand for Z-A
      case "Height: High - Low":
        result = mountains.sort((a, b) => {
          if (a.height > b.height) {
            return -1;
          } else {
            return 1;
          }
        });
        break;
      case "Height: Low - High":
        result = mountains.sort((a, b) => {
          if (a.height > b.height) {
            return 1;
          } else {
            return -1;
          }
        });
        break;
      case "Difficulty: High - Low":
        result = mountains.sort((a, b) => a.difficulty > b.difficulty); //this is the shorthand when you're justing returning one thing
        console.log(result);
        break;
      case "Difficulty: Low - High":
        result = mountains.sort((a, b) => {
          if (a.difficulty > b.difficulty) {
            return 1;
          } else {
            return -1;
          }
        });
        break;
      default:
        break;
    }
    setMountains([...result]); //spreading creates a new instance which causes the App to re-render in a new order
  };

  return (
    <header className="header">
      <nav className="nav">
        <div className="nav-header">
          <BackButton />
          <Title className="title" />
          {isListerPage && (
            <button className="nav-toggle menu-btn" alt="nav toggle">
              <FaBars
                className="fa-bars"
                onClick={() => setActiveFilter(!activeFilter)}
              />
            </button>
          )}
        </div>

        <Menu
          mountains={mountains}
          setMountains={setMountains}
          activeFilter={activeFilter}
          setActiveFilter={setActiveFilter}
        />
        {isListerPage && (
          <div className="second-nav">
            <Search
              mountains={mountains}
              setMountains={setMountains}
              searchString={searchString}
              setSearchString={setSearchString}
            />
            <form className="sort-form">
              <label className="label" htmlFor="cars">
                Sort
              </label>
              <select
                name="sort"
                className="sort"
                onChange={(e) => handleSort(e.target.value)}
              >
                {sort.map((sort) => (
                  <Sort key={sort} sort={sort} />
                ))}
              </select>
            </form>
          </div>
        )}
      </nav>
    </header>
  );
};

export default Header;
