import React from "react";

const Views = ({ setViews, pdpMountain }) => {
  return (
    <div>
      <label className="centered-label pdp-label">Views</label>
      <div>
        <textarea
          className="input view"
          type="text"
          placeholder="What other peaks can you see?"
          onBlur={(e) => setViews(e.target.value)}
          defaultValue={pdpMountain.views}
        ></textarea>
      </div>
    </div>
  );
};

export default Views;
