import Header from "./Components/Lister/Header/Header";
import { useState, useEffect } from "react";
import Footer from "./Components/Footer/Footer";
import state from "./State/State";
import ListView from "./Components/Lister/ListView/ListView";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import HomePage from "./Components/HomePage/HomePage";
import MountainPage from "./Components/PDP/MountainPage";

function App() {
  const [initialMountains, setInitialMountains] = useState(
    window.localStorage.setItem("initialMountains", JSON.stringify(state))
  );

  const [mountains, setMountains] = useState(
    JSON.parse(window.localStorage.getItem("mountains")) ||
      JSON.parse(window.localStorage.getItem("initialMountains"))
  );

  const [filteredMountains, setFilteredMountains] = useState(mountains);

  useEffect(() => {
    window.localStorage.setItem("mountains", JSON.stringify(mountains));
  }, [mountains]);

  useEffect(() => {
    window.localStorage.setItem("mountains", JSON.stringify(filteredMountains));
  }, [filteredMountains]);

  const [activeFilter, setActiveFilter] = useState(false);
  const [selectedMountain, setSelectedMountain] = useState(0);

  const handleRightClick = (index, mountainName) => {
    // const mountain = mountains.id === id;
    const mountain = mountains[index];
    console.log(index, filteredMountains, mountainName);
    if (
      mountain.name === mountainName &&
      mountain.imageNumber < mountain.image.length - 1
    ) {
      let newData = mountains;
      newData[index].imageNumber = newData[index].imageNumber + 1;
      console.log(newData[index].image);
      setMountains([...newData]); //spreading creates a new instance which causes the App to re-render in a new order. This fixes the issue when the page only renders on re-saving
      // setImageNumber((imgNum) => ++imgNum);//use index + 1 or arrow function with the ++ applied pre
      console.log(newData[index].imageNumber);
    } else {
      let newData = mountains;
      newData[index].imageNumber = newData[index].imageNumber = 0;
      setMountains([...newData]);
    }
  };

  const handleLeftClick = (index, mountainName) => {
    const mountain = mountains[index];
    console.log(index, filteredMountains, mountainName);
    if (mountain.name === mountainName && mountain.imageNumber > 0) {
      let newData = mountains;
      newData[index].imageNumber = newData[index].imageNumber - 1;
      console.log(newData[index].image);
      setMountains([...newData]); //spreading creates a new instance which causes the App to re-render in a new order. This fixes the issue when the page only renders on re-saving
      // setImageNumber((imgNum) => ++imgNum);//use index + 1 or arrow function with the ++ applied pre
      console.log(newData[index].imageNumber);
    } else {
      let newData = mountains;
      newData[index].imageNumber = mountains[index].image.length - 1;
      setMountains([...newData]);
    }
  };

  const handleClickThrough = (index) => {
    //grabbing the index which is selected and passing down as props lower down
    setSelectedMountain(index);
    // console.log(selectedMountain);
  };

  return (
    <Router>
      <div className="App">
        <Switch>
          <Route exact path="/">
            <HomePage />
          </Route>
          <Route exact path="/lister">
            <Header
              mountains={mountains}
              setMountains={setFilteredMountains}
              setActiveFilter={setActiveFilter}
              activeFilter={activeFilter}
            />
            <ListView
              mountains={filteredMountains}
              setMountains={setFilteredMountains}
              handleLeftClick={handleLeftClick}
              handleRightClick={handleRightClick}
              setActiveFilter={setActiveFilter}
              activeFilter={activeFilter}
              handleClickThrough={handleClickThrough}
            />
            <Footer />
          </Route>
          <Route exact path="/pdp">
            {/* <Header /> */}
            <MountainPage
              mountains={filteredMountains}
              setMountains={setFilteredMountains}
              selectedMountain={selectedMountain}
              setActiveFilter={setActiveFilter}
              handleLeftClick={handleLeftClick}
              handleRightClick={handleRightClick}
            />
            <Footer />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
