import React from "react";

const TotalWalkTimeInput = ({
  pdpMountain,
  totalWalkTime,
  setTotalWalkTime,
}) => {
  return (
    <div>
      <label className="pdp-label">Total Walk Time</label>
      <input
        className="input total-time"
        type="time"
        onBlur={(e) => setTotalWalkTime(e.target.value)}
        defaultValue={
          pdpMountain.totalWalkTime === ""
            ? totalWalkTime
            : pdpMountain.totalWalkTime
        }
      />
    </div>
  );
};

export default TotalWalkTimeInput;
