import React from "react";

const DescentDurationInput = ({ pdpMountain, setDescentDuration }) => {
  return (
    <div>
      <label className="pdp-label">Decent Duration</label>
      <input
        className="input"
        type="time"
        defaultValue={pdpMountain.descentDuration}
        onBlur={(e) => setDescentDuration(e.target.value)}
      />
    </div>
  );
};

export default DescentDurationInput;
