import React from "react";

const Climbers = ({ pdpMountain, setClimbers }) => {
  return (
    <div className="climbers">
      <label className="centered-label pdp-label">Climbers</label>
      <div>
        <textarea
          className="input climber"
          type="text"
          placeholder="Who climbed this mountain?"
          onBlur={(e) => setClimbers(e.target.value)}
          defaultValue={pdpMountain.climbers}
        ></textarea>
      </div>
    </div>
  );
};

export default Climbers;
