// import Facets from "./Facets";
import React from "react";
import RegionFacetGroups from "./RegionFacetGroups";
import CompletionStatusFacetGroups from "./CompletionStatusFacetGroups";
import DoneButton from "./DoneButton";

const Menu = ({ mountains, setMountains, activeFilter, setActiveFilter }) => {
  // const [regions, setRegions] = useState([
  //   "Central",
  //   "Eastern",
  //   "Far Eastern",
  //   "Northern",
  //   "Southern",
  //   "Western",
  // ]);

  return (
    <div
      className={`filter-container ${
        activeFilter ? "show-filter-container" : ""
      }`}
    >
      <RegionFacetGroups name="Region" />
      <CompletionStatusFacetGroups name="Completion Status" />
      <DoneButton
        mountains={mountains}
        setMountains={setMountains}
        activeFilter={activeFilter}
        setActiveFilter={setActiveFilter}
      />
    </div>
  );
};

export default Menu;
