import { Link } from "react-router-dom";

const HomePage = () => {
  return (
    <div class="container container-center">
      <img src="images/homepage mobile.jpg" className="image" alt="mountains" />
      <p className="homepage-title">
        Wainwright
        <br /> Tracker
      </p>
      {/* Link prevents requests to the server and allows react to manage the routing */}
      <Link to="/lister" className="homepage-btn">
        Get Tracking
      </Link>
    </div>
  );
};

export default HomePage;
