import { React, useState, useEffect } from "react";
import Climbers from "./Climbers";
import TopInputFields from "./TopInputFields";
import BottomInputFields from "./BottomInputFields";
import Views from "./Views";
import CompleteButton from "./CompleteButton";
import ImageCarousel from "../Lister/ListView/ImageCarousel";
import DescentDurationInput from "./DescentDurationInput";
import FinishTimeInput from "./FinishTimeInput";
import TotalWalkTimeInput from "./TotalWalkTimeInput";
import BackButton from "../Lister/Header/BackButton";
import Title from "../Lister/Header/Title";

const MountainPage = ({
  mountains,
  setMountains,
  selectedMountain,
  handleLeftClick,
  handleRightClick,
}) => {
  // console.log(selectedMountain);
  //filter returns an array so results[0] is the mountain shown on the page. This then gets reassigned to pdpMountain
  const pdpMountain = mountains[selectedMountain];

  const [climbers, setClimbers] = useState("");
  const [date, setDate] = useState("");
  const [startTime, setStartTime] = useState("");
  const [ascentDuration, setAscentDuration] = useState("");
  const [summitTime, setSummitTime] = useState("");
  const [views, setViews] = useState("");
  const [descentDuration, setDescentDuration] = useState("");
  const [finishTime, setFinishTime] = useState("");
  const [totalWalkTime, setTotalWalkTime] = useState("");

  const [topInputValues, setTopInputValues] = useState([
    {
      title: "Date",
      state: date,
      setState: setDate,
      stateName: pdpMountain.date,
    },
    {
      title: "Start Time",
      state: startTime,
      setState: setStartTime,
      stateName: pdpMountain.startTime,
    },
    {
      title: "Ascent Duration",
      state: ascentDuration,
      setState: setAscentDuration,
      stateName: pdpMountain.ascentDuration,
    },
    {
      title: "Summit Time",
      state: summitTime,
      setState: setSummitTime,
      stateName: pdpMountain.summitTime,
    },
  ]);

  // const [bottomInputValues, setBottomInputValues] = useState([
  //   {
  //     title: "Descent Duration",
  //     state: descentDuration,
  //     setState: setDescentDuration,
  //     stateName: pdpMountain.descentDuration,
  //   },
  //   {
  //     title: "Finish Time",
  //     state: finishTime,
  //     setState: setFinishTime,
  //     stateName: pdpMountain.finishTime,
  //   },
  //   {
  //     title: "Total Walk Time",
  //     state: summitTime,
  //     setState: setTotalWalkTime,
  //     stateName: pdpMountain.totalWalkTime,
  //   },
  // ]);

  const handleComplete = () => {
    const newData = mountains;
    pdpMountain.complete = !pdpMountain.complete;
    setMountains([...newData]);
    handleUpdate();
  };

  const handleUpdate = () => {
    setMountains((mountains) => {
      return mountains.reduce((acc, mountain) => {
        if (mountain.name === pdpMountain.name) {
          return [
            ...acc,
            {
              ...mountain,
              climbers: climbers,
              date: date,
              ascentDuration: ascentDuration,
              startTime: startTime,
              summitTime: summitTime,
              views: views,
              descentDuration: descentDuration,
              finishTime: finishTime,
              totalWalkTime: totalWalkTime,
            },
          ];
        } else {
          return [...acc, mountain];
        }
      }, []);
    });
    window.history.back();
  };

  useEffect(() => {
    if (summitTime === "" || descentDuration === "") {
      return;
    }
    function convertNST(summitTime) {
      const arr = summitTime.split(":");
      const seconds = arr[0] * 3600 + arr[1] * 60;
      return seconds;
    }

    function convertDD(descentDuration) {
      const arr = descentDuration.split(":");
      const seconds = arr[0] * 3600 + arr[1] * 60;
      return seconds;
    }

    const finishTimeCalc = convertNST(summitTime) + convertDD(descentDuration);

    var measuredTime = new Date(null);
    measuredTime.setSeconds(finishTimeCalc); // specify value of SECONDS
    var finishTimeString = measuredTime.toISOString().substr(11, 8);
    console.log(finishTimeString);

    setFinishTime(finishTimeString);
  }, [summitTime, descentDuration]);

  useEffect(() => {
    if (ascentDuration === "" || descentDuration === "") {
      return;
    }
    function convertAD(ascentDuration) {
      const arr = ascentDuration.split(":");
      const seconds = arr[0] * 3600 + arr[1] * 60;
      return seconds;
    }

    function convertDD(descentDuration) {
      const arr = descentDuration.split(":");
      const seconds = arr[0] * 3600 + arr[1] * 60;
      return seconds;
    }

    const totalWalkTimeCalc =
      convertAD(ascentDuration) + convertDD(descentDuration);

    var measuredTime = new Date(null);
    measuredTime.setSeconds(totalWalkTimeCalc); // specify value of SECONDS
    var totalWalkTimeString = measuredTime.toISOString().substr(11, 8);

    setTotalWalkTime(totalWalkTimeString);
  }, [ascentDuration, descentDuration]);

  return (
    <div>
      <header className="header">
        <nav className="nav">
          <div className="nav-header">
            <BackButton handleUpdate={handleUpdate} />
            <Title className="title" />
          </div>
        </nav>
      </header>
      <div className="main-section">
        <section className={`${pdpMountain.name}`}>
          <div className="mountain-border">
            <div className="name">{pdpMountain.name}</div>
            <ImageCarousel
              pdpMountain={pdpMountain}
              handleLeftClick={handleLeftClick}
              handleRightClick={handleRightClick}
              selectedMountain={selectedMountain}
            />
            <div className="mountain-info">
              <form className="pdp-form">
                <Climbers pdpMountain={pdpMountain} setClimbers={setClimbers} />
                <div className="first-pdp-container">
                  {topInputValues.map((value) => (
                    <TopInputFields
                      pdpMountain={pdpMountain}
                      value={value}
                      summitTime={summitTime}
                      key={mountains.id}
                      startTime={startTime}
                      ascentDuration={ascentDuration}
                      setSummitTime={setSummitTime}
                    />
                  ))}
                </div>
                <Views setViews={setViews} pdpMountain={pdpMountain} />
                <div className="second-pdp-container">
                  {/* {bottomInputValues.map((value) => ( */}
                  {/* <BottomInputFields
                  pdpMountain={pdpMountain}
                  // value={value}
                  totalWalkTime={totalWalkTime}
                  finishTime={finishTime}
                  descentDuration={descentDuration}
                  setDescentDuration={setDescentDuration}
                  setFinishTime={setFinishTime}
                  setTotalWalkTime={setTotalWalkTime}
                /> */}
                  {/* ))} */}
                  <DescentDurationInput
                    pdpMountain={pdpMountain}
                    setDescentDuration={setDescentDuration}
                  />
                  <FinishTimeInput
                    pdpMountain={pdpMountain}
                    finishTime={finishTime}
                    setFinishTime={setFinishTime}
                  />
                  <TotalWalkTimeInput
                    pdpMountain={pdpMountain}
                    totalWalkTime={totalWalkTime}
                    setTotalWalkTime={setTotalWalkTime}
                  />
                </div>
                <CompleteButton
                  handleComplete={handleComplete}
                  pdpMountain={pdpMountain}
                />
              </form>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
};

export default MountainPage;
