import React from "react";
import { FaCheck } from "react-icons/fa";
import { useState } from "react";
import classnames from "classnames";

const Facets = ({ name }) => {
  const [ticked, setTicked] = useState(false);

  //alternative for the classnames detailed below
  // let classes ='fa-check';
  // classes += ticked ? " show-fa-check" : ""

  return (
    //the below effectively toggles between what the state is at present. So, ticked would be not ticked and vice versa
    <div
      className="filter"
      onClick={() => {
        setTicked((state) => !state);
        // addToArray();
      }}
    >
      <FaCheck
        className={classnames("fa-check", {
          "show-fa-check": ticked === true,
        })}
        alt="filter ticked"
      />
      <p>{name}</p>
    </div>
  );
};
export default Facets;
