const Footer = () => {
  //dynamically add the date
  const date = new Date().getFullYear(); //setting a date variable which can be used within HTML

  return (
    <div>
      <footer class="footer">
        JM Development Inc&copy;.<span class="date">{date} </span>{" "}
      </footer>
      <br />
    </div>
  );
};

export default Footer;
