import React from "react";
import { Link } from "react-router-dom";
import ImageCarousel from "./ImageCarousel";

const ListView = ({
  mountains,
  setMountains,
  handleLeftClick,
  handleRightClick,
  activeFilter,
  setActiveFilter,
  handleClickThrough,
}) => {
  if (!mountains) {
    return null; //this is a safety net to say if mountains doesn't exist return nothing
  }

  const handleComplete = (index) => {
    const newData = mountains;
    newData[index].complete = !newData[index].complete;
    setMountains([...newData]);
  };

  return mountains.map((mountain, index) => (
    <section className={mountain.name} id={mountain.name} key={mountain.id}>
      <div
        className={`mountain-border ${activeFilter && "opaque-background"}
        ${mountain.complete === true ? "show-mountain-border" : ""}`}
      >
        <div className="name">{mountain.name}</div>
        <ImageCarousel
          mountain={mountain}
          handleLeftClick={handleLeftClick}
          handleRightClick={handleRightClick}
          index={index}
        />
        <div className="mountain-info">
          <ul>
            <li className="mountain-region">Region: {mountain.region}</li>
            <li className="height">Height (ft): {mountain.height}</li>
            <li className="average-time">
              Average Walk Time: {mountain.averageTime}
            </li>
            <li className="difficulty stars">
              Difficulty: {mountain.difficulty}
            </li>
          </ul>
          <div
            className={mountain.complete ? "incomplete btn" : "complete btn"}
            onClick={() => handleComplete(index)}
          >
            {mountain.complete ? "Mark as Incomplete" : "Mark as Complete"}
          </div>
          <Link
            to="/pdp"
            className="more-info btn"
            onClick={() => handleClickThrough(index)}
          >
            Add More Info
          </Link>
        </div>
      </div>
      <hr className="seperator" />
    </section>
  ));
};

export default ListView;
