import { FaSearch } from "react-icons/fa";
import { React, useState } from "react";

const Search = ({ mountains, setMountains }) => {
  const [searchString, setSearchString] = useState("");

  const handleFilter = (e) => {
    e.preventDefault();
    console.log(e.target.value);
    setSearchString(e.target.value.toLowerCase());
    //const searchString = e.target.value.toLowerCase();
    console.log(searchString);
    console.log(searchString.length);
    const searchMountains = mountains.filter((mountain) => {
      //console.log(searchString.length);
      if (searchString.length === 0) {
        return mountains;
      } else {
        console.log(mountain.name);
        return mountain.name.toLowerCase().includes(searchString);
      }
    });
    // console.log(searchMountains);
    setMountains(searchMountains);
  };

  return (
    <form className="search-form">
      <FaSearch className="fa-search" />
      <input
        //onInput={(e) => setSearchString(e.target.value)}
        onKeyUp={handleFilter}
        className="input-field search"
        alt="search field"
        type="text"
        placeholder="Find Wainwright"
        name="search"
        minLength="1"
        required=""
        defaultValue={searchString}
      />
    </form>
  );
};

export default Search;
