import React from "react";

const CompleteButton = ({ handleComplete, pdpMountain }) => {
  return (
    <div
      className={pdpMountain.complete ? "incomplete btn" : "complete btn"}
      onClick={() => handleComplete()}
    >
      {pdpMountain.complete ? "Mark as Incomplete" : "Mark as Complete"}
    </div>
  );
};

export default CompleteButton;
